import React from 'react';
import { useTranslation } from 'react-i18next';

import { useNotificationsSettings } from '@mssgme/hooks';
import { useProductsContext } from '../../../hooks';
import { NotificationsSettingsForm } from '../forms';
import { ProductsSettingsForm } from '../ProductsSettingsForm';

export function ProductsNotifications() {
    const { t } = useTranslation('products');
    const { router, crm } = useProductsContext();
    const { enabled } = useNotificationsSettings(crm);

    return (
        <ProductsSettingsForm
            successRedirectPath={router.settings.toSettings()}
            backPath={router.settings.toSettings()}
            title={t('ProductsNotifications.title')}
            showSubmit={enabled}
        >
            <NotificationsSettingsForm />
        </ProductsSettingsForm>
    );
}
