import PropTypes from 'prop-types';
import React, { useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import { useToggle } from 'react-use';

import { useActions, useGlobalMessengers, useWorkspace, useRestriction } from '@mssgme/hooks';
import {
    EnhancedFormik,
    Form,
    ModalEditPane,
    renderNode,
    RestrictionScope,
    SaveButton,
    usePresenceRemoval,
} from '@mssgme/ui';
import { useProductsContext } from '../../../hooks';

const pickNonEmpty = (list) => {
    for (const resolver of list) {
        const items = !resolver || Array.isArray(resolver) ? resolver : resolver();

        if (items?.length) {
            return items;
        }
    }

    return [];
};

export function ProductsSettingsForm({
    title,
    backPath,
    children,
    successRedirectPath,
    initialData = {},
    initialDirty = false,
    showSubmit = true,
    restrictRule = null,
    ...rest
}) {
    const history = useHistory();
    const { card: { messengers } = {} } = useWorkspace();
    const defaultMessengers = useGlobalMessengers();
    const { api } = useActions();
    const { t } = useTranslation();
    const { router, specifier: { website }, crm, setEntry } = useProductsContext();
    const initial = useMemo(
        () => ({
            defaultCurrency: crm.defaultCurrency || 'USD',
            notifications: crm.notifications || {
                email: true,
                telegram: true,
                webhook: true,
            },
            onBoarding: crm.onBoarding,
            onBoardingStep: crm.onBoardingStep,
            contacts: {
                messengers: pickNonEmpty([
                    crm.contacts.messengers,
                    defaultMessengers?.map(({ type, platform, value }) => ({ type, platform, value })),
                    messengers?.map((messenger) => ({
                        type: messenger.type,
                        platform: messenger.messenger,
                        value: messenger.name,
                    })),
                ]),
            },
            payments: crm.payments || {},
            form: {
                successMessage: '',
                fields: [],
                ...crm.form,
                successURL: {
                    title: crm.form?.successURL?.title || '',
                    action: crm.form?.successURL?.action || {
                        type: 'url',
                        url: '',
                    },
                },
            },
            ...initialData,
        }),
        [crm, defaultMessengers, messengers]
    );
    const { handleBlockedEvent } = useRestriction(restrictRule, crm || initial);
    const [isClosing, setClosing] = useToggle(false);
    backPath = backPath || router.toHome();

    usePresenceRemoval();

    const handleSubmit = useCallback(
        async (values) => {
            let redirect = successRedirectPath || backPath;
            const entryUrl = router.isBackOverriddenBack && router.toEntry();

            if (website) {
                await api.websites.crm.settings.update({
                    websiteId: website,
                    payload: values,
                });
            } else {
                await api.crm.settings.update({ payload: values });
            }

            // todo: this is a workaround for context reset
            if (entryUrl) {
                setEntry(entryUrl);
            }

            setClosing(true);

            if (typeof redirect === 'function') {
                redirect();
            } else {
                setTimeout(() => history.push(redirect), 200);
            }
        },
        [website, successRedirectPath, backPath]
    );

    usePresenceRemoval();

    return (
        <EnhancedFormik initialValues={initial} onSubmit={handleSubmit} {...rest}>
            {(form) => (
                <ModalEditPane
                    guardChanges={!(isClosing || handleBlockedEvent)}
                    onClose={backPath}
                    title={title}
                >
                    <RestrictionScope>
                        <Form autoComplete="off" style={{ display: 'flex', flexDirection: 'column', flex: 1 }}>
                            {React.Children.map(children, (child) => child && renderNode(child, form))}

                            {showSubmit && (
                                <SaveButton
                                    fullWidth
                                    size="standart"
                                    isInitialHidden={false}
                                    disabled={isClosing || !(initialDirty || form.dirty)}
                                    style={{ marginTop: 16 }}
                                    isFetching={form.isSubmitting}
                                    onClick={handleBlockedEvent}
                                >
                                    {t('ui:labels.apply')}
                                </SaveButton>
                            )}
                        </Form>
                    </RestrictionScope>
                </ModalEditPane>
            )}
        </EnhancedFormik>
    );
}

ProductsSettingsForm.propTypes = {
    title: PropTypes.string.isRequired,
    backPath: PropTypes.any.isRequired,
    successRedirectPath: PropTypes.any,
    children: PropTypes.any.isRequired,
    initialData: PropTypes.object,
    initialDirty: PropTypes.bool,
    showSubmit: PropTypes.bool,
    restrictRule: PropTypes.string,
};
